@import "Styles-components.css";

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background-color: var(--white);
    font-family: "Fira Code";
}

@media (max-width: 800px) {
    html {
        font-size: 12px;
    }

    main {
        min-width: 450px;
    }

    .buttons {
        flex-direction: column-reverse;
        gap: 1rem;
    }

    .task-input {
        font-size: 13.5px;
    }
}