:root {
    --yellow: hsl(29, 100%, 68%);
    --red: hsl(360, 100%, 62%);
    --white: white;
    --black: hsl(0, 0%, 19%);
}

main {
    display: flex;
    flex-direction: column;
    justify-content: center;
    min-width: 800px;
    flex-basis: 800px;
    margin: 0 auto;
    padding: 3rem;
    background-color: var(--black);
    border-radius: 15px;
}

.title {
    background-color: var(--yellow);
    padding: 1rem 1.5rem;
    font-size: 5rem;
    text-align: center;
    color: var(--white);
    border-radius: 15px;
    cursor: default;
    user-select: none;
}

.input-and-button {
    position: relative;
    display: flex;
    gap: 20px;
    margin-bottom: 4rem;
}

.ascii-message {
    position: absolute;
    bottom: -30px;
    color: var(--red);
}

.task-input {
    width: 100%;
    height: 4rem;
    padding: 0 1rem;
    border-radius: 8px;
    border: none;
    font-family: inherit;
    font-size: 24px;
}

.task-input:focus {
    outline: none;
    box-shadow: 0 0 1rem var(--yellow);
}

.clear-input {
    background-color: transparent;
    padding-inline: 1rem;
    font-family: inherit;
    font-size: 1.5rem;
    font-weight: bold;
    color: var(--white);
    border: 3px solid var(--red);
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.2s ease-in;
}

.clear-input:hover {
    background-color: var(--red);
}

.clear-input:active {
    transform: scale(0.8);
}

.buttons {
    display: flex;
    justify-content: space-evenly;
    margin-bottom: 1.5rem;
}

.input-button {
    padding: 1.2rem 3rem;
    background-color: transparent;
    border-radius: 8px;
    border: 3px solid var(--yellow);
    font-size: 1.5rem;
    font-family: inherit;
    font-weight: bold;
    color: var(--white);
    cursor: pointer;
    transition: background-color 0.2s ease-in, border 0.2s ease-in;
}

.input-button:nth-of-type(1):hover {
    background-color: var(--red);
    border: 3px solid var(--red);
}

.input-button:nth-of-type(2):hover {
    background-color: var(--yellow);
    color: var(--white);
}

.input-button:active {
    transform: scale(0.8);
}

.pending-tasks {
    padding: 1rem 0;
    text-align: center;
    font-size: 1.4rem;
    color: var(--white);
}

.list-items {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: flex-start;
    gap: 5px;
    line-height: 3rem;
    border-top: 3px solid var(--white);
    white-space: pre;
}

.todo-task {
    position: relative;
    margin-top: 15px;
    padding: 2px 0 2px 15px;
    font-size: 1rem;
    color: var(--white);
    border: 2px solid var(--yellow);
    border-radius: 8px;
    transition: background-color 0.3s ease-out, color 0.3s ease-out;
    user-select: none;
}

.todo-task:hover {
    background-color: var(--yellow);
    color: var(--black);
}

.cross-sign {
    position: absolute;
    right: 10px;
    font-size: 1.5rem;
    padding: 0 10px;
    color: var(--red);
    cursor: pointer;
}